taskInput.addEventListener('keyup', function (evt) {
  if (evt.key === 'Enter') {
    addToShoppingList(evt.target.value);
    renderShoppingList();
    this.value = '';
  }
});

shoppingListDiv.addEventListener('click', function (evt) {
  if (evt.target.parentElement.classList.contains('priority-control')) {
    const priority = evt.target.classList.value;
    const taskId = evt.target.parentElement.parentElement.getAttribute(
      'data-id'
    );
    setPriority(taskId, priority);
    renderShoppingList();
  }

  if (evt.target.classList.contains('status')) {
    const taskId = evt.target.parentElement.getAttribute('data-id');
    console.log(taskId);
  }
});

shoppingListDiv.addEventListener('dragstart', function (evt) {
  if (evt.target.classList.contains('item')) {
    const getId = evt.target.getAttribute('data-id');
    evt.dataTransfer.setData('text/plain', getId);
  }
});

completedDiv.addEventListener('drop', function (evt) {
  const taskId = evt.dataTransfer.getData('text/plain');
  const getTask = shoppingList.filter((item) => item.id === taskId)[0];
  completedList = [getTask, ...completedList];
  shoppingList = shoppingList.filter((item) => item.id !== taskId);
  renderShoppingList();
  renderCompletedList();
});

completedDiv.addEventListener('dragover', function (evt) {
  evt.preventDefault();
});
