let shoppingList = [];
let completedList = [];

export const addToShoppingList = (task) => {
  const taskId = `${parseInt(
    Math.random() * 100000000
  )}-${new Date().getTime()}`;

  shoppingList.push({
    task,
    id: taskId,
    priority: 'normal',
  });
};

export const addToCompletedList = (taskId) => {
  const getTask = shoppingList.find(({ id }) => id === taskId);
  shoppingList = shoppingList.filter(({ id }) => id !== taskId);
  completedList = [getTask, ...completedList];
};

export const setPriority = (taskId, priority) => {
  shoppingList = shoppingList.map((item) => {
    if (item.id === taskId) {
      return {
        ...item,
        priority,
      };
    }

    return item;
  });
};

export const getShoppingList = () => shoppingList;
export const getCompletedList = () => completedList;
