import { getShoppingList, getCompletedList } from './dataModel';
import Task from './Task';

const shoppingListDiv = document.querySelector('.shopping-list');
const completedDiv = document.querySelector('.completed');

export const renderShoppingList = () => {
  const domNodes = getShoppingList().map((item) => {
    return Task(item.task, item.priority, item.id);
  });
  shoppingListDiv.innerHTML = domNodes.join('');
};

export const renderCompletedList = () => {
  const domNodes = getCompletedList().map((item) => {
    return Task(item.task, item.priority, item.id);
  });

  completedDiv.innerHTML = domNodes.join('');
};
